code,
pre {
  font-size: $code-font-size;
  font-family: $code-font-family;
  color: $code-color;
  padding: 5px;
  background: $code-background-color;

  .dark-theme & {
    color: $code-color-dark;
    background: $code-background-color-dark;
  }
}

.highlight > pre.chroma {
  padding: 38px 7px 8px;
  overflow-x: auto;
}

.highlight > .chroma {
  margin: 1em 0;
  position: relative;
  font-family: $code-font-family;

  code {
    padding: 0;
  }

  pre {
    margin: 0;
    padding: 38px 7px 8px;
    overflow-x: visible;
  }

  &::after {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    padding: 2px 7px;
    font-size: $code-font-size;
    font-weight: bold;
    color: $code-info-color;
    background: darken($code-background-color, 3%);
    content: 'Code';

    .dark-theme & {
      color: $code-info-color-dark;
      background: darken($code-background-color-dark, 3%);
    }
  }

  @each $type, $text in $code-type-map {
    &.#{$type}::after {
      content: $text;
    }
  }

  .lntd {
    // Fix code block null line height and
    // Synchronous gutter and code line highly.
    line-height: round($code-font-size * 1.5);

    &:first-child {
      width: 10px;
    }

    &:last-child {
      vertical-align: top;
    }
  }

  table, tr, td {
    margin: 0;
    padding: 0;
    width: 100%;
    border-collapse: collapse;
    border-color: $code-background-color;

    .dark-theme & {
      border-color: $code-background-color-dark;
    }
  }

  /* LineNumbersTable */
  .lnt {
    color: $code-info-color;
  }

  /* LineHighlight */
  .hl {
    display: block;
    background-color: darken($code-background-color, 5%);

    .dark-theme & {
      background-color: darken($code-background-color-dark, 5%);
    }
  }
}

.highlight {
