/**Footer**/
.copyright {
  font-size: .875rem;

  .copyright-line {
    width: 100%;

    .icp-br {
      display: none;
    }
  }
}

.dynamic-to-top {
  display: none;
  overflow: hidden;
  width: auto;
  z-index: 90;
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  top: auto;
  left: auto;
  font-family: sans-serif;
  font-size: 1rem;
  color: #fff;
  text-decoration: none;
  text-shadow: 0 1px 0 #333;
  font-weight: bold;
  padding: 1rem 1rem;
  border: 1px solid $global-border-color;
  background: #222;
  &:hover {
    background: #000;
    cursor: pointer;
  }
  &:active {
    background: #000;
    outline: none;
  }
  outline: none;
  &:focus, &:hover {
    outline: none;
  }
  span {
    display: block;
    overflow: hidden;
    width: .875rem;
    height: .75rem;
    background: url('data:image/png;base64,') no-repeat center center;
  }

  .dark-theme &{
    border: 1px solid $global-border-color-dark;
  }
}
